import { motion } from "framer-motion";
import { applyForBorrowMembership } from "../../api/utsavApis";
import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import { useToast } from "../../contexts/toastContext";
import useForm from "../../hooks/useForm";

const BecomeUtsav = () => {
  const toast = useToast();
  const onSubmitHandler = async (formData) => {
    const { code } = formData;

    // client side validations
    if (!code) return;

    try {
      const response = await applyForBorrowMembership(code);
      if (response?.status) {
        console.log(response.data);
        toast.success("Request sent successfully");
        clearForm();
      }
    } catch (error) {
      toast.error("Failed to send request");
      console.log(error);
    }
  };

  const { formData, handleChange, handleSubmit, clearForm } = useForm(
    {},
    onSubmitHandler
  );

  return (
    <motion.div
      className="w-full min-h-screen sm:p-8 md:px-12 flex flex-col items-center"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="bg-white p-6 md:p-10 rounded-md lg:shadow-md w-full">
        <h1 className="text-3xl font-semibold text-center mb-8">
          Utsav Membership
        </h1>

        <div className="bg-blue-50 p-6 rounded-md border border-blue-300 mb-10">
          <h2 className="text-xl font-medium mb-4">Instructions</h2>
          <ul className="list-decimal list-inside text-gray-700 space-y-2">
            <li>Please purchase from UTSAV to activate UTSAV.</li>
            <li>
              One has to purchase UTSAV products of a minimum amount of â‚¹4999.
            </li>
            <li>
              After that, you can purchase multiple products available in UTSAV.
            </li>
          </ul>
        </div>

        <div className="bg-gray-50 p-6 rounded-md border border-gray-300">
          <h2 className="text-xl font-medium mb-6">
            Apply for Borrow Membership
          </h2>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-gray-700 font-medium mb-2">
                Referral Code
              </label>
              <InputBox
                placeholder="Enter referral code"
                name="code"
                value={formData.code}
                onChange={handleChange}
                className="h-10 w-full px-4 rounded-md text-sm bg-white border border-gray-300 focus:ring-blue-500 focus:border-blue-500"
              />
            </div>

            <Button className="h-10 w-full text-white rounded-md">apply</Button>
          </form>
        </div>
      </div>
    </motion.div>
  );
};

export default BecomeUtsav;
